import React from 'react'
import { useSelector } from 'react-redux'

import PropTypes from 'prop-types'

import './InvoicesTable.styles.scss'
// import LineBlock from '../../../lib/components/LineBlock'
import RowLineTab from './RowLineTab'
// import Button from '../../../lib/components/Button/Button'
// import CheckboxColor from '../../../lib/components/CheckboxColor/CheckboxColor'
import ExtendedTab from './ExtendedTab/ExtendedTab'
/**
 * param type (?id=1232339&type=desadv&mode=add)
 */
const InvoicesTable = React.memo(({ type }) => {
  const { invocesTable } = useSelector((state) => state.desAdv)

  if (!invocesTable) {
    return <div>loading..</div>
  }
  return (
    <>
      <div className="tab-block">
        <table className="tcnt-order" cellspacing="4" border="1">
          <thead>
            {Object.keys(invocesTable.header).length && (
              <RowLineTab isHeader values={invocesTable.header} />
            )}
          </thead>
          <tbody>
            {invocesTable.data.length &&
              invocesTable.data.map((values, index) => (
                <RowLineTab
                  isHeader={false}
                  key={values.id['value'] + values.EAN['value']}
                  values={values}
                  odd={index}
                />
              ))}
          </tbody>
        </table>
      </div>
      <ExtendedTab />
    </>
  )
})

