//   name: 'Название(1)',
//   data: {
//     name: 'Номер заказа',
//     value: 'regress2506-2',
//     type: 'div',
//     valueBold: false,
//   },
// }

InvoicesTable.propTypes = {
  type: PropTypes.string,
}

InvoicesTable.defaultProps = {
  type: 'order',
}

export default InvoicesTable
